"use client";
import { useEffect, useRef } from 'react';

export default function HeroSection () {
  const textRef = useRef(null); // Reference to the text element
  const sentence = "We build products that shape a better future"; // Your sentence

  useEffect(() => {
    const element = textRef.current;
    let index = 0;

    const type = () => {
      if (index < sentence.length) {
        element.innerHTML += sentence.charAt(index); // Add one character at a time
        index++;
        setTimeout(type, 100); // Type each letter with a delay of 100ms
      }
    };

    type(); // Start the typing effect
  }, []); // Only run once when the component mounts

  return (
    <div className="pt-16">
      <div className="flex flex-col bg-cover h-screen text-white lg:px-28 lg:py-10 sm:px-5 sm:py-2" style={{ backgroundImage: 'url("/images/background.jpg")' }}>
        <div className="bg-white bg-opacity-10 w-full px-10 py-10" style={{ borderRadius: "55px" }}>
          <h1
            ref={textRef} // The reference to control the text
            className="sm:text-[3.5rem] lg:text-[5rem] min-h-[180px] font-normal leading-[1] w-[80%]">
            {/* The text will be typed out here */}
          </h1>
          <p className="mt-4 mb-8 leading-[1] text-base md:text-lg text-[#aea9b1] w-[75%]">
            Weâ€™re the architects of digital excellence across industries. We redefine business with cutting-edge digital strategies that invoke sector-wide transformation.
          </p>
          <button className="hidden md:block bg-white text-black font-semibold px-6 py-3 rounded-full hover:bg-[#60a6e7] hover:text-white">
            Book a Call
          </button>
        </div>
      </div>
    </div>
  )
}
